@import url(https://fonts.googleapis.com/css?family=Archivo);

.App {
  	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 80px;
}

.App-header {
	background-color: #222;
	height: 150px;
	padding: 20px;
	color: white;
}

.App-title {
  	font-size: 1.5em;
}

@-webkit-keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* New css class added */

.hidden{
	display: none !important;
}

.color-red{
	color: red;
}

.container{
	width: 20%;
}

.main-btn{
	font-family: 'Archivo';
	font-weight: bold;
	font-size: 50px;
	background-color:#00b3a1;
	color: white;
	border-radius: 10px;
	padding: 20px;
	width: 100%;
	height: 100px;
}

.main-btn:hover{
	-webkit-transition: all 1000ms cubic-bezier(0.420, 0.000, 1.000, 1.000); 
	-o-transition: all 1000ms cubic-bezier(0.420, 0.000, 1.000, 1.000); 
	transition: all 1000ms cubic-bezier(0.420, 0.000, 1.000, 1.000); /* ease-in */

	-webkit-transition-timing-function: cubic-bezier(0.420, 0.000, 1.000, 1.000); 
	-o-transition-timing-function: cubic-bezier(0.420, 0.000, 1.000, 1.000); 
	transition-timing-function: cubic-bezier(0.420, 0.000, 1.000, 1.000); /* ease-in */
		
	font-size: 49px;
	-webkit-filter: brightness(95%);
	        filter: brightness(95%);
}

p{
	font-size: 20px;
}

.personCard{
	display: inline-block;
	background-color: lightgray;
	border-radius: 10px;
	width: 10%;
	padding: 10px;
	margin: 20px;
}

.paging-btn{
	display: inline-block;
	padding: 20px 30px;
	margin: 5px;
	font-weight: 900;
	font-size: 25px;
	background-color: #03A1FF;
}

.input-text-field{
	display: inline-block;
	padding: 20px 30px;
	margin: 5px;
	font-weight: 900;
	font-size: 25px;
	width: 2%;
	text-align: center;
}

.required:after { content:" *"; }


